import axios from 'axios';

import {

  GET_OWNER_HOMES
} from './types';

  // Get owner homes
  export const getOwnerHomes = () => dispatch => {
    dispatch();
    axios
      .get('http://localhost:3001/OwnerHomes')
      .then(res =>
        
        dispatch({
          type: GET_OWNER_HOMES,
          payload: res.data
          
        })
      )
      .catch(err =>
        dispatch({
          type: GET_OWNER_HOMES,
          payload: null
        })
      );
  };




