import React, { Component } from 'react';
import PropTypes from 'prop-types';


class OwnerHomeItem extends Component {

  render() {
    const { profile } = this.props;

    return (
      
        <div className="row">
          <div className="col-3">
            <div className="row">
                <div className="col-2">
                    <i className="fas fa-home text-info mr-1" />
                </div>
                <div className="col-10 text-left">
                    <p>{profile.houseName.substring(0,27)}</p>
                </div>
           
            </div>
          </div>
          <div className="col-2 text-center">
            <p>{profile.guestCapacity} guests</p>
          </div>
          <div className="col-2 text-center">
            <p>{profile.houseLocation}</p>
          </div>
          
          <div className="col-2 text-center">
            <p>{profile.houseArea}sqft</p>
          </div>
          <div className="col-2 text-center">
            <p>{profile.housePrice}/night</p>
          </div>
          <div className="col-1">
            
          </div>
        </div>
      
    );
  }
}

OwnerHomeItem.propTypes = {
  profile: PropTypes.object.isRequired
};

export default OwnerHomeItem;



