import { 
    GET_OWNER_HOMES
  } from '../actions/types';
  
  const initialState = {
    ownerhomes: null
    
  };
  
  export default function(state = initialState, action) {
    switch (action.type) {

      case GET_OWNER_HOMES:
      return {
        ...state,
        profiles: action.payload,
        ownerhomes: false
      };

      default:
        return state;
    }
  }
  


