import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import HomeItem from './HomeItem';
import { getHomes } from '../../actions/profileActions';

class Homes extends Component {
    componentDidMount() {
        this.props.getHomes();
      }
    
      render() {
        const { profiles, loading } = this.props.profile;
        let homeItems;
    
        if (profiles === null || loading) {
            homeItems = <h4 style={{textAlign:'center', paddingBottom:'214px'}}>No homes have been added by the Owner</h4>;
        } else {
         if (profiles.length > 0) {
            homeItems = profiles.map(profile => (
              <HomeItem key={profile._id} profile={profile} />
            ));

          } else {
            homeItems = <h4>No homes have been added by the Owner</h4>;
          }
        }
    
        return (
          <div className="homes">
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  
                  <h2 className="display-8 text-center">Homes Available</h2>
                  <p className="lead text-center">
                    <i>Find the best homes around the Country for affordable prices</i>
                  </p>
                  {homeItems}
                  <Link to="/dashboard" className="btn btn-light float-right" >
                    Dashboard
                  </Link>
                </div>
              </div>
            </div>
          </div>
        );
      }
    }
    
    Homes.propTypes = {
      getHomes: PropTypes.func.isRequired,
      profile: PropTypes.object.isRequired
    };
    
    const mapStateToProps = state => ({
      profile: state.profile
    });
    
    export default connect(mapStateToProps, { getHomes })(Homes);





