import axios from 'axios';

import {
  GET_PROFILE,
  GET_PROFILES,
  PROFILE_LOADING,
  CLEAR_CURRENT_PROFILE,
  GET_ERRORS,
  GET_OWNER_HOMES
} from './types';

// Get current profile
export const getCurrentProfile = () => dispatch => {
  dispatch(setProfileLoading());
  axios
    .get('http://localhost:3001/profile')
    .then(res =>
      dispatch({
        type: GET_PROFILE,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_PROFILE,
        payload: {}
      })
    );
};

// Create Profile
export const createProfile = (profileData, history) => dispatch => {
    axios
      .post('http://localhost:3001/profile', profileData)
      .then(res => history.push('/dashboard'))
      .catch(err =>
        dispatch({
          type: GET_ERRORS,
          payload: err.response.data
        })
      );
  };

  //add Home
  export const addHomes = (homeData, history) => dispatch => {
    axios
      .post('http://localhost:3001/addhome', homeData)
      .then(res => history.push('/owner-homes'))
      .catch(err =>
        dispatch({
          type: GET_ERRORS,
          payload: err.response.data
        })
      );
  };

  // Get all homes
export const getHomes = () => dispatch => {
  dispatch(setProfileLoading());
  axios
    .get('http://localhost:3001/allHomes')
    .then(res =>
      dispatch({
        type: GET_PROFILES,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_PROFILES,
        payload: null
      })
    );
};
  
  // Get owner homes
  export const getOwnerHomes = () => dispatch => {
    dispatch(setProfileLoading());
    axios
      .get('http://localhost:3001/OwnerHomes')
      .then(res =>
        
        dispatch({
          type: GET_OWNER_HOMES,
          payload: res.data
          
        })
      )
      .catch(err =>
        dispatch({
          type: GET_OWNER_HOMES,
          payload: null
        })
      );
  };

// Profile loading
export const setProfileLoading = () => {
    return {
      type: PROFILE_LOADING
    };
  };

  // Clear profile
export const clearCurrentProfile = () => {
    return {
      type: CLEAR_CURRENT_PROFILE
    };
  };


