import React, { Component } from 'react';
import PropTypes from 'prop-types';


class HomeItem extends Component {
  render() {
    const { profile } = this.props;

    return (
      <div className="card card-body bg-light mb-3">
        <div className="row">
          <div className="col-3">
            <img src={profile.houseImage} alt="" />
          </div>
          <div className="col-lg-7 col-md-7 col-8">
            <h3>{profile.houseName}</h3>
            <p>{profile.houseDescription.substring(0,150)}</p>
            <br/>
            <h5>{profile.houseLocation}</h5>
            <p><i>Home owned by {profile.houseOwnerName}</i></p>
          </div>
          <div className="col-lg-2 col-md-2 col-8">
            <h5>Beds : {profile.guestCapacity}</h5>
            <h6>{profile.houseArea}sqft</h6>
            <br/>
            <br/>
            <h3>${profile.housePrice}/Night</h3>
          </div>
        </div>
      </div>
    );
  }
}

HomeItem.propTypes = {
  profile: PropTypes.object.isRequired
};

export default HomeItem;



